.App {
  text-align: center;
  background-color: black;
  color: rgb(250, 250, 250);
  height: 100%;
  min-height: 100vh;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
  margin: 5px 25px;
  position: relative;
  z-index: -1;
}
.App-title {
  font-size: 1.25em;
  margin: 0 25px;
  letter-spacing: 1rem;
  font-style: italic;
}
.Home, .Timers, .Tasks, .Agenda {
  padding-top: 55px;
}
.selected-nav {
  color: white;
  text-decoration: underline;
}
.Timers li {
  border: none;
}
.Timers li button {
  padding: 5px;
}

button {
  font-size: 1.5rem;
  margin: 5px;
  color: white;
  background-color: rgb(75, 50, 40);
  -webkit-transition: .333s ease background-color;
  -o-transition: .333s ease background-color;
  transition: .333s ease background-color;
  border: 2px solid white;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  /*
  display: block;
  margin: 0 auto;
  width: 300px;
  height: 2rem;
  padding: 0 5px;*/
}
button:hover, button:active {
  background-color: rgb(90, 65, 55);
}
h1#timer-string {
  margin: 5px auto;
  font-size: 5rem;
}
h3 {
  margin: 5px;
  letter-spacing: .1rem;
}
header, nav {
  background-color: #222;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-bottom: 100px;
  position: fixed;
}
header a, nav a {
  color: rgba(250, 250, 250, .75);
  text-decoration: none;
  -webkit-transition: .333s ease all;
  -o-transition: .333s ease all;
  transition: .333s ease all;
}
header a:focus {
  outline: none;
}
header {
  top: 0;
  left: 0;
  height: 25px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
nav {
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  top: 25px;
  font-size: 1.5rem;
}
nav:hover {
  color: white;
}
ul {
  text-align: left;
  list-style: none;
  padding-left: 0;
  margin: 10px auto 20px auto;
  width: 500px;
}
li {
  padding: 0 2px;
  font-size: 1.25rem;
  border: 1px dotted white;
}
li ul li {
  font-size: 1.1rem;
  border: none;
}
li:hover {
  text-shadow: 5px solid black;
}

.tasks-nav {
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  margin: 0 25px;
  display: -ms-flexbox;
  display: flex;
  text-decoration: none;
}
.tasks-nav button {
  border-radius: 20px;
  padding: 5px 15px;
  font-size: 1.2rem;
}
.selected-tasks-nav, .selected-tasks-nav button {
  visibility: hidden;
}

#start-button {
  background-color: #0EBC10;
  font-weight: bold;
  letter-spacing: .2rem;
  width: 150px;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}
#select-task {
  font-size: 1.2rem;
}
#different-task {
  display: none;
}
#task-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
#task-list li {
  margin: 10px 0;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width : 850px) {
  ul {
    margin: 10px 5px 25px 5px;
    width: 98%;
  }
  .App-logo {
    margin: 5px 10px;
  }
  .Timers li {
    margin: 10px;
  }
}


